body{
  font-family: "Times New Roman";
  display: flex;
  justify-content: center;
}
.container{
  width: 1092px;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.offer{
  display: flex;
  width: 100%;
  justify-content: space-around;
  height: 200px;
}
.menu__title{
  text-align: center;
}
.menu__main{
  padding: 0 0;
  cursor: pointer;
  position: relative;
  border: 1px solid grey;
  border-radius: 5px;
  width: 200px;
  background-color: #e8e8e9;
  height: 40px;
}
.menu__block{
  padding: 10px 0 10px 10px;
  border-top: 1px solid grey;
}
.menu__selected{
  padding: 10px 0 10px 10px;
}
.triangle-down{
  right: 2px;
  top: 15px;
  position: absolute;
  border: 7px solid transparent;
  border-top: 10px solid grey;
}
.triangle-left{
  left: 16px;
  top: 15px;
  position: absolute;
  border: 7px solid transparent;
  border-left: 10px solid grey;
}
.triangle-right{
  left: 9px;
  top: 15px;
  position: absolute;
  border: 7px solid transparent;
  border-right: 10px solid grey;
}
.counter{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.counter__main{
  display: flex;
}
.counter__block{
  position: relative;
  cursor: pointer;
  outline: none;
  width: 40px;
  height: 40px;
  background-color: #e8e8e9;
}
.counter__number{
  text-align: center;
  font-size: 30px;
  width: 40px;
  height: 40px;
}
.hint{
    margin-top: 5px;
    padding: 0;
}
.price__text{
    text-align: center;
    font-size: 25px;
}
.buy__button{
    cursor: pointer;
    margin-top: 30px;
    padding-top: 6px;
    text-align: center;
    border-radius: 10px;
    border: 1px solid grey;
    font-size: 25px;
    width: 200px;
    height: 40px;
    background-color: #e8e8e9;
}